@import url('https://fonts.googleapis.com/css2?family=Inter&family=Roboto:wght@500&display=swap');
.Footer_main{
  display: flex;
  width: 100%;
  height: 200px;
  background-color: black;
  margin-top: 50px;
  flex-direction: column;
  color: white;
  align-items: center;
  text-align: justify;
  padding-bottom: 50px;
  font-family: 'Roboto', sans-serif;
}
.Footer1 {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  column-gap: 50px;
  text-align: justify;
  letter-spacing: 0.2em;
}
.menu {
  display: flex;
  justify-content: center;
  color: white;
}

.menu .A-text {
  position: relative;
  display: block;
  padding: 5px;
  color: white;
}

.menu .A-text::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(to right, #363636, #ffffff, #000000);
      box-shadow: 0 0 10px #ffffff,
                0 0 40px rgb(255, 255, 255),
                0 0 80px #ffffff;
  z-index: 1;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease-in-out;
}

.menu .A-text:hover::before {
  transform: scaleX(1);
}

.menu[data-animation="to-left"] .A-text::before {
  transform-origin: right;
}

.menu[data-animation="center"] .A-text::before {
  transform-origin: center;
}

.menu[data-animation="bonus"] .A-text::before {
  transform-origin: right;
}

.menu[data-animation="bonus"] .A-text:hover::before {
  transform-origin: left;
  transform: scaleX(1);
  transition-timing-function: cubic-bezier(0.2, 1, 0.82, 0.94);
}


@media only screen and (max-width: 550px) {
  .Footer_main{
    display: flex;
    width: 100%;
    height: 200px;
    background-color: black;
    margin-top: 50px;
    flex-direction: column;
    color: white;
    align-items: center;
    text-align: justify;
    padding-bottom: 50px;
    font-family: 'Roboto', sans-serif;
  }
  .Footer1 {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    column-gap: 20px;
    text-align: justify;
    letter-spacing: 0.2em;
    padding-left: 10%;
    padding-right: 10%;
    width: 80%;
    flex-wrap: wrap;
  }
  .Copy_right {
    padding-top: 5%;
    padding-bottom: 5%;
    width: 85%;
    font-size: 15px;
  }
}